<template>
	<div id="goodsSearch">
    <div class="header">
      <!--<div class="van-hairline&#45;&#45;bottom van-nav-bar">-->
        <!--<div class="van-nav-bar__left">-->
          <!--<i class="van-icon van-icon-arrow van-nav-bar__arrow" @click="$router.back(-1)" style="color: #000000 !important;"></i>-->
        <!--</div>-->
        <!--<div class="van-ellipsis van-nav-bar__title">-->
          <!--<van-search v-model="value" placeholder="搜索" @search="getGoodsList"/>-->
        <!--</div>-->
        <!--&lt;!&ndash;<div class="van-nav-bar__right" @click="forReverse">&ndash;&gt;-->
          <!--&lt;!&ndash;<img src="static/img/icon_px_splb11.png" style="height: 16px;vertical-align: middle;">&ndash;&gt;-->
        <!--&lt;!&ndash;</div>&ndash;&gt;-->
      <!--</div>-->
      <van-row gutter="20" style="height: 44px;display: flex;align-items: center;">
        <van-col span="4">
          <van-icon name="arrow-left" @click.native="$router.go('-1')"/>
        </van-col>
        <van-col span="16" class='search font_12' style="background:#ffffff;">
          <van-search v-model="value" placeholder="搜索" @search="getGoods"/>
        </van-col>
      </van-row>
    </div>
    <div class="login_box">
      <div style="height: 5px;width: 100%"></div>
      <van-list v-model="loading" :finished="finished" @load="getGoodsList">
        <van-card v-for="(item, index) in goodsList" :key="index" style="margin: 10px;" @click.native="toGoods(item)">
          <div slot="thumb">
            <img :src="item.goodslogo.split(',')[0]" v-lazy="item.goodslogo.split(',')[0]" width="100%" style="display: block;"/>
          </div>
          <div slot="tags" class="font_weight_bold padding_bottom_5">
            {{item.goodsname}}
          </div>
          <div slot="tags" class="font_12 color_8b8b8b" style="height: 46px;overflow: auto">
            {{item.introduce}}
          </div>
          <div slot="tags" class="font_12" style="padding-top: 5px;">
            <span class="color_ff3312" v-if="item.type==1">¥{{item.price}}</span>
            <span class="color_ff3312" v-if="item.type==2">¥{{item.spike}}</span>
            <span class="float_right color_8b8b8b">{{item.paynum}}人付款</span>
          </div>
        </van-card>
      </van-list>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'goodsList',
		data() {
			return {
        loading: false,
        finished: false,
        userId:localStorage.getItem('userId'),
        value:'',
        goodsList:[],
        pageNum: 1,
			}
		},
    mounted() {
		  this.value = this.$route.query.value;
      this.getGoodsList();
    },
		methods:{
      getGoods(){
        this.pageNum = 1;
        this.goodsList = [];
        this.loading = false;
        this.finished = false;
        this.getGoodsList();
      },
      toGoods(dataMap){
        if(dataMap.type==1){
          this.$store.commit('setGoodsInfo', dataMap);
          this.$router.push('goods');
        }else{
          this.$store.commit('setGoodsInfo', dataMap);
          this.$router.push('goodsKill');
        }
      },
      getGoodsList(){
        this.loading = true;
        let $this = this;
        let obj = {
          goodsname: $this.value,
          pageNum: this.pageNum
        };
        let qs = require('qs');
        $this.$ajax.post("goods/getgoodslist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            // $this.goodsList = rpdata.data.data.data;
            if(rpdata.data.data.data.length>0){
              $this.goodsList = $this.goodsList.concat(rpdata.data.data.data);
              $this.pageNum++;
              $this.loading = false;
            }else if(rpdata.data.data.data.length==0){
              $this.finished = true;
              $this.loading = false;
            }
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  #goodsSearch .header {
    height: 44px;
    box-shadow: 0px 2px 10px lightgrey;
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99;
    background-color: white;
  }
  #goodsSearch .header img {
    vertical-align: text-top;
  }
  #goodsSearch .search {
    border: 1px solid lightgray;
    border-radius: 30px;
  }
  #goodsSearch .header .van-col {
    text-align: center;
  }
  #goodsSearch .van-search {
    padding: 0px;
  }
  #goodsSearch .van-search {
    background-color: transparent!important;
  }
  #goodsSearch .login_box{
    margin-top: 44px;
  }
</style>



